import { Directive, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import {
  AppNameService, EventbusService, MainBarTitleService,
  scrollToTop, WindowService
} from '@front-shared-lib';
import {
  navByBus,
  VendorManager
} from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { ListComposeAdminPanel } from '../../defines/composition/list';

@Directive()
export class VendorManagerListBase
implements OnInit, OnDestroy {
  listCompose = new ListComposeAdminPanel<VendorManager>(
    VendorManager,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  actions = [
    {
      label: 'View',
      icon: 'visibility',
      event: (vendorManager: VendorManager) => this.viewVendorManager(vendorManager)
    },
    {
      label: 'Update',
      icon: 'edit',
      event: (vendorManager: VendorManager) => this.updateVendorManager(vendorManager)
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (vendorManager: VendorManager) => this.listCompose
        .openDeleteOneModal(
          vendorManager._id,
          'Delete Vendor Manager',
          'Are You Sure You Want To Delete This Vendor Manager?'
        )
    }
  ];

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  constructor(
    protected router: Router,
    protected eventbus: EventbusService,
    protected windowService: WindowService,
    protected toastr: ToastrService,
    protected apNme: AppNameService,
    protected mainBarTitle: MainBarTitleService,
    protected metaTagService: Meta,
    protected titleService: Title
  ) {}

  ngOnInit(): void {
    this.mainBarTitle.title = 'Vendor Managers - List of Vendor Managers';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `This page provides an overview of vendor managers,
      allowing users to manage and view vendor manager details effectively.`
    });

    this.listCompose.getDataArr();
    scrollToTop(this.windowService);
  }

  viewVendorManager(vendorManager: VendorManager) {
    this.navigateByBus(['/vendor-manager/view/' + vendorManager.urId], 'oneItem', vendorManager);
  }

  updateVendorManager(vendorManager: VendorManager) {
    this.navigateByBus(['/vendor-manager/update'], 'updateVendorManager', vendorManager);
  }

  subscribeToPkg(id: string) {
    this.router.navigate(['subscribe/' + id]);
  }

  addVendorManager() {
    this.router.navigate(['/vendor-manager/add']);
  }

  getUser(vm: VendorManager) {
    return vm.manager;
  }

  ngOnDestroy() {
    this.listCompose.cleanup?.();
  }
}

